import { useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import { logout } from "../../redux/actions/auth_action";
import classes from "./DropDown.module.scss"

const DropDown = ({profile}) => {
  
  const dispatch = useDispatch()


  return ( <div className={classes.DropDown}>
    <Link onClick={profile} to="/orders"> </Link>
    <Link onClick={profile} to="profile"></Link>
    <div></div>
    <span onClick={()=>{profile(); dispatch(logout())}}></span>
  </div> );
}
 
export default DropDown;